<template>
  <div>
    <h6 v-if="loading" class="text-primary">loading ...</h6>
    <h6 v-if="error" class="text-danger">There was a problem loading data</h6>

    <div v-if="response && !loading">
      <h4>{{response.setup}}</h4>
      <p>{{response.punchline}}</p>
    </div>

    <button @click="fetchData"
      :class="{'disabled': loading}"
      class="btn btn-info">
      Another one
    </button>
  </div>
</template>

<script>
import axios from 'axios'
let apiUrl = 'https://08ad1pao69.execute-api.us-east-1.amazonaws.com/dev/random_joke'

export default {
  data () {
    return {
      loading: true,
      error: false,
      response: null
    }
  },
  mounted () {
    this.fetchData()
  },
  methods: {
    fetchData () {
      this.loading = true
      this.error = false

      axios
        .get(apiUrl)
        .then(response => {
          this.response = response.data
        })
        .catch(e => {
          this.error = true
          console.log(e)
        })
        .finally(resp => {
          this.loading = false
        })
    }
  }
}
</script>



